import React from 'react';
import { withStyles, makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

const temp = makeStyles({
  body: {
    backgroundColor: (props) =>  props.backgroundColor,
    fontSize: 14,
  },
  border2: {
    border: "none"
  },
  border1: {

  }
});

let facN = 10;

const StyledTableCell = withStyles((theme) => ({
  head: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
  body: {
    backgroundColor: (props) =>  props.backgroundColor,
    fontSize: 14,
  },
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
  root: {
    '&:nth-of-type(odd)': {
      backgroundColor: theme.palette.action.hover,
    },
  },
}))(TableRow);


const generate = (n) => {
  let list = [1]
  for(let i = 1; i <= n; i++) {
    list.push(i * list[i - 1]);
  }
  list.splice(0, 1);
  return list;
}

const generateList = (n) => {
  let list = []
  for (let i = 1; i <= n; i++) {
    list.push(i);
  }
  return list;
}

const generateDict = (name, n) => {
  let dict = new Map();
  dict.set("name", name);
  let list = generate(n);
  for (let i = 1; i <= n; i ++) {
    dict.set(i, list[i-1].toString());
  }
  return dict;
}

// n 
const generateSequenceDict = (n=undefined) => {
  let dict = new Map();
  dict.set("name", "");
  for (let i = 1; i <= n; i ++) {
    dict.set(i, "");
  }
  if (n) {
    dict.set(n, "" + (n + 1));
  }
  return dict;

}

const rows = [
  generateDict("Fac", facN),
    generateSequenceDict(5)
  // createData('Ice cream sandwich', 23700, 9.0, 37, 4.3),
  // createData('Eclair', 262, 16.0, 24, 6.0),
  // createData('Cupcake', 305, 3.7, 67, 4.3),
  // createData('Gingerbread', 356, 16.0, 49, 3.9),
];

const generateTable = (row) => {
  let list = [];
  // eslint-disable-next-line no-unused-vars
  for (let [key, value] of row) {
    if (key !== "name") {
      list.push(value)
    }
  }
    console.log(list)

  return list;
}

const useStyles = makeStyles({
  table: {
    // minWidth: "100px",
    // // display: "flex",
    width: "auto",
    margin: "auto",
    boxShadow: "none"
  },
});

export default function CustomizedTables(props) {
  const classes = useStyles();
  // const props = {"backgroundColor": "lightblue"};
  const mod = temp(props);
  return (
    <TableContainer
        component={Paper}
        className={classes.table}
    >
      <Table className={classes.table} aria-label="customized table">
        <TableHead>
          <TableRow>

            <StyledTableCell align="center">N-th</StyledTableCell>
            {generateList(facN).map(x=>(<StyledTableCell  align="center">{x}</StyledTableCell>))}

          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row, index) => (
              <StyledTableRow key={row.get("name")}>
              <StyledTableCell className={`${ index === 0 ? mod.border1: mod.border2}`} component="th" scope="row" align="center">
                {row.get("name")}
              </StyledTableCell>
                {generateTable(row).map(x=>(<TableCell className={`${ index === 0 ? mod.border1: mod.border2}`} align={"center" }>{x}</TableCell>)
                )}
            </StyledTableRow>)
          )}
        </TableBody>
      </Table>
    </TableContainer>
  );
}
